<template>
  <div>
    <div class="pc">
      <div class="music">
        <el-row style="padding-bottom: 10px;margin-left: 15px;margin-top: 10px">
          <el-upload
            class="avatar-uploader"
            ref="upload"
            :show-file-list="false"
            action=""
            :before-upload="beforeUpload"
            :on-change="handleChange"
            :auto-upload="false"
          >
            <el-button size="small" type="primary" icon="el-icon-upload2" style="height: 30px;font-size: 16px;">上传</el-button>
          </el-upload>
        </el-row>
        <div style="border-bottom: 1px solid rgb(243,243,243)"><p style="margin-left: 15px;margin-bottom: 10px">全部音频:</p></div>
        <div class="list_type_name1" style="border-bottom: 1px solid rgb(243,243,243)">
          <div class="list_type_name" style="width: 60%"><span style="padding-left: 30px;display: inline-block;height: 40px;line-height: 40px">文件名</span></div>
          <div class="list_type_name" style="width: 10%"><span style="padding-left: 15px;display: inline-block;height: 40px;line-height: 40px">大小</span></div>
          <div class="list_type_name" style="width: 15%"><span style="padding-left: 15px;display: inline-block;height: 40px;line-height: 40px">上传日期</span></div>
          <div class="list_type_name" style="width: 15%"><span style="padding-left: 15px;display: inline-block;height: 40px;line-height: 40px">作者</span></div>
        </div>
        <div v-if="displayMusicImg" style="width: 350px;height:400px;background-color: rgba(0,0,0,0.6);position: absolute;z-index: 9999;left: 40%;top: 30%">
          <div class="block">
            <el-image
              class="img"
              :src="'http://47.112.132.177:8092/qrcode/createCommonQRCode?id='+musicId"
              style="height: 300px;width: 300px;margin-left: 25px;margin-top: 25px"
            >
            </el-image>
            <span style="color: white;margin-left: 130px;margin-top: 15px;display: inline-block" class="erweima">扫码下载文件</span>
          </div>
        </div>
        <div v-loading="musicLoding" style="height: 700px" class="main_list">
          <div v-if="!musicLoding">
            <div v-if="musics.length>0">
              <div v-for="(music,index) in musics" :key="index">
                <li :id="'music'+music.id" class="music_lists" @mouseover="()=>showP(music.id)" @mouseout="()=>hideP(music.id)">
                  <i class="el-icon-headset" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <span style="display: inline-block;width: 15%;float:right;font-size: 14px;margin-top: 13px;text-indent: 1em;overflow: hidden;height: 20px;">{{music.createBy}}</span>
                  <span style="display: inline-block;width: 15%;float:right;font-size: 14px;margin-top: 13px;text-indent: 1em;overflow: hidden;height: 20px;">{{music.fileUrlTime.split("T")[0]}}</span>
                  <span style="display: inline-block;width: 10%;float:right;font-size: 14px;margin-top: 13px;text-indent: 1em;overflow: hidden;height: 20px;">{{countSize(music.fileSize)}}</span>
                  <span class="list_name" style="display: inline-block;width: 45%;margin-top: 13px;overflow: hidden;height: 20px;" @click="()=>clickMusic(music.id)">{{music.fileRealName}}</span>
                  <span :id="'mp'+music.id" style="display: inline-block;position: absolute;left: 50%;margin: 0;" v-show="false">
                <a :href="'http://47.112.132.177:8092/file/download?id='+music.id"><i style="font-size: 20px!important;cursor: pointer;margin: 0px 5px;color: rgb(77,151,255);width: 20px;" class="el-icon-download list_hover"/></a>
                <i class="el-icon-share list_hover" style="font-size: 20px!important;cursor: pointer;margin: 0px 5px;color: rgb(77,151,255);" @click="()=>downloadMusic(music.id)"/>
                <i class="el-icon-delete list_hover" style="font-size: 20px!important;cursor: pointer;margin: 0px 5px;color: rgb(77,151,255);" @click="()=>deleteMusic(music.id)"/>
              </span>
                </li>
              </div>
            </div>
            <div v-else>
              <p style="margin:0;margin-top: 20%;margin-left: 38%;display: inline-block;font-size: 28px;">还未上传任何音乐</p>
            </div>
          </div>
        </div>
        <div class="block" v-if="musicPage">
          <el-pagination
            layout="prev, pager, next"
            :total="musicPage.totalSize"
            :current-page="musicPage.pageNum"
            :page-size="17"
            :hide-on-single-page="true"
            @current-change="(currentPage)=>reqMessages(currentPage)"
            style="margin-left: 40%"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="mo">
      <div class="mo_image">
        <div class="mo_allName">
          <p>全部音频</p>
        </div>
        <div class="main" v-loading="musicLoding">
          <div v-show="showMusic" style="width: 250px;height:300px;background-color: rgba(0,0,0,0.6);position: absolute;z-index: 9999;left: 17%;top: 27%">
            <div style="height: 20px;width: 20px;border-radius: 20px;color: white;border: 2px solid white;text-align: center;line-height: 20px;float: right;margin: 10px 10px 15px 0px" @click="showMusic = false">X</div>
            <div>
              <el-image
                class="img"
                :src="'http://47.112.132.177:8092/qrcode/createCommonQRCode?id='+musicId"
                style="height: 200px;width: 200px;margin-left: 25px;"
              >
              </el-image>
              <span style="color: white;margin-left: 80px;margin-top: 15px;display: inline-block" class="erweima">扫码下载文件</span>
            </div>
          </div>
          <div v-if="!musicLoding">
            <div v-if="mo.musics.length>0">
              <div v-for="(music,index) in mo.musics" :key="index">
                <li :id="'image'+music.id" style="min-height: 50px;max-height: 70px;border-bottom: 1px solid rgb(230,230,230)">
                  <i class="el-icon-headset" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <div style="display: inline-block;width: 63%;margin-top: 10px;margin-bottom: 5px;margin-right: 5%;">
                    <p style="display: inline-block;width: 100%;overflow: hidden;min-height: 16px;max-height: 32px;font-size: 14px;line-height:16px;overflow: hidden">{{music.fileRealName}}</p>
                    <span style="display: inline-block;width: 45%;overflow: hidden;min-height: 15px;max-height: 15px;font-size: 12px;color: lightgrey">{{music.fileUrlTime.split("T")[0]}}</span>
                    <span style="display: inline-block;width: 40%;overflow: hidden;min-height: 15px;max-height: 15px;font-size: 12px;color: lightgrey">{{countSize(music.fileSize)}}</span>
                  </div>
                  <span :id="'mp'+music.id" style="display: inline-block;width:18%;position: relative">
                    <el-popover
                      placement="top-start"
                      width="100"
                      trigger="click"
                      style="margin-top: -15px;position: absolute"
                    >
                      <a :href="'http://47.112.132.177:8092/file/download?id='+music.id" style="width: 20px;margin-left: 30px;margin: 0px 7px;"><i style="font-size: 20px!important;color: rgb(77,151,255);width: 20px;" class="el-icon-download list_hover"/></a>
                      <i class="el-icon-share list_hover" style="font-size: 20px!important;margin: 0px 7px;color: rgb(77,151,255);" @click="()=>downloadMusic(music.id,true)"/>
                      <i class="el-icon-delete list_hover" style="font-size: 20px!important;margin: 0px 7px;color: rgb(77,151,255);" @click="()=>deleteMusic(music.id)"/>
                      <i class="el-icon-more-outline" style="font-size: 20px!important;margin: 0px 5px;color: rgb(77,151,255);position: absolute;top: -25px" slot="reference"></i>
                    </el-popover>
                  </span>
                </li>
              </div>
            </div>
            <div v-else>
              <p style="margin:0;margin-top: 30%;margin-left: 24%;display: inline-block;font-size: 18px;">还未上传任何音频</p>
            </div>
            <div class="block" v-if="mo.musicPage">
              <el-pagination
                layout="prev, pager, next"
                :total="mo.musicPage.totalSize"
                :current-page="mo.musicPage.pageNum"
                :page-size="mo.pageSize"
                :hide-on-single-page="true"
                @current-change="(currentPage)=>reqMessages(currentPage)"
                :small="true"
                style="float:right;margin-right: 3%;margin-top: 10px"
              >
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import '../assets/my-message.css'
  import {RewYearTypeEnable,ChangeEnable} from '../api/types'
  export default {
    data(){
      return {
        showMusic:false,
        musics:[],
        musicPage:[],
        enable:1,
        musicId:0,
        musicLoding:false,
        page:1,
        pageSize:17,
        type:4,
        enable:1,
        displayMusicImg:false,
        mo:{
          musics:[],
          page:1,
          pageSize:11,
          musicPage:null,
        }
      }
    },
    props:{
      reReq:{
      }
    },
    async mounted () {
      this.$emit('choiceType','1-4')
      this.$emit('choicetypemo','3')
      this.reqMessages(1)
      document.documentElement.onclick=(e)=>{     //点击其他的地方收起表情或者修改文字大小
        if($(e.target).attr('class')!='el-icon-share list_hover')
          this.displayMusicImg = false
      }
    },
    methods:{
      showP(e){
        $('#mp'+e).show()
      },
      hideP(e){
        $('#mp'+e).hide()
      },
      downloadMusic(id,mo=false){
        if(mo){
          this.musicId = id
          this.showMusic = true
        }else{
          this.musicId = id
          this.displayMusicImg = true
        }
      },
      deleteMusic(id){
        this.musicId = id
        this.$confirm('确定删除选中的文件吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          var result = await ChangeEnable(0,this.musicId)
          if(result.status==200){
            this.musicLoding = true
            this.reqMessages(1)
          }else{
            this.$message(
              {
                type: 'error',
                message: '删除失败，请重试',
                duration: 1000,
              }
            )
          }
        }).catch(() => {
          return false
        });
      },
      clickMusic(id){
        this.musicId = id
        this.displayMusicImg = true
      },
      countSize(size){
        if(size/1024/1024/1024>0.5){
          return ((size/1024/1024/1024)+'').split('.')[0]+'.'+((size/1024/1024/1024)+'').split('.')[1].slice(0,2)+'G'
        } else if(size/1024/1024>0.5){
          return ((size/1024/1024)+'').split('.')[0]+'.'+((size/1024/1024)+'').split('.')[1].slice(0,2)+'M'
        }else if(size/1024>0.5){
          return ((size/1024)+'').split('.')[0]+'.'+((size/1024)+'').split('.')[1].slice(0,2)+'KB'
        }else{
          return size + 'B'
        }
      },
      async reqMessages(page){
        this.musicLoding = true
        var mo = this.mo
        var result = await RewYearTypeEnable(this.enable,this.type,page,this.pageSize)
        if(result.status){
          if(result.status == 200){
            result = result.obj
            this.musics = result.content
            this.musicPage = result
          }
        }else{
          this.$message(
            {
              type: 'error',
              message: '暂时无法获取数据，请刷新重试',
              duration: 2000,
            }
          )
        }
        var result1 = await RewYearTypeEnable(this.enable,this.type,page,mo.pageSize)
        if(result1.status){
          if(result1.status == 200){
            result1 = result1.obj
            mo.musics = result1.content
            mo.musicPage = result1
          }
        }else{
          this.$message(
            {
              type: 'error',
              message: '暂时无法获取数据，请刷新重试',
              duration: 2000,
            }
          )
        }
        this.musicLoding = false
      },
      handleChange (file, fileList) {
        this.$emit('myUpload',file.raw)
      },
      beforeUpload (file) {
        return true
      },
    },
    watch:{
      reReq(newval){
        if(newval&&newval==this.type){
          this.reqMessages(1)
          this.$emit('noreReq',false)
        }
      }
    }
  }
</script>

<style scoped>
  @media screen and (max-width: 499px){
    .block{
      height: 45px;
      padding-top: 5px;
    }
    .block:after,.block:before{
      display: table;
      content: '';
      clear: both;
    }
    .pc{
      display: none;
    }
    .mo{
      display: block;
    }
    .mo_image{
      height: 100%;
      width: 100%;
      background-color: white;
    }
    .mo_allName{
      height: 30px;
      margin-bottom: 10px;
    }
    .mo_allName p{
      height: 30px;
      line-height: 30px;
      font-size: 15px;
      font-weight: bold;
      margin-left: 10px;
    }
    .main{
      min-height: 300px;
    }
  }
  @media screen and (min-width: 500px){
    .pc{
      display: block;
    }
    .mo{
      display: none;
    }
    .music_lists{
      height: 40px;
      position: relative;
      border-bottom: 1px solid rgb(243,243,243);
    }
    .music_lists:hover,.list_type_name:hover{
      background-color: rgb(244,251,255);
    }
    .list_type_name{
      float: left;
      height: 40px;
      color: gray;
      cursor: pointer;
    }
    .main_list{
      height: 782px
    }
    .list_type_name1:after,.list_type_name1:before{
      display: table;
      content: '';
      clear: both;
    }
    .music_lists .list_name{
      font-size: 14px;
      margin-left: 10px;
      position: relative;
      cursor: pointer;
    }
    .music_lists .list_name:hover{
      color: rgb(64,158,254);
      text-decoration: underline;
    }
    .music_lists i{
      line-height: 40px;
      height: 40px;
      margin-left: 30px;
    }
  }
  .music_lists span,.list_type_name,.erweima{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
</style>



