<template>
  <div class="loginall">
    <div class="login">
      <div class="login1">
        <p>海创网盘</p>
        <el-form :model="ruleForm"  :rules="rules" ref="ruleForm" label-width="100px">
          <el-form-item label="账户"  prop="account">
            <el-input  prefix-icon="el-icon-s-custom" v-model.number="ruleForm.account"><</el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input prefix-icon="el-icon-lock" type="password" v-model="ruleForm.password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="go('ruleForm')" style="height: 40px;width:100%;padding: 0px 10px">登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
  import {login} from '../api/types'
  import store from 'store'
  export default {
    data() {
      var checkAccount = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('账号不能为空'));
        }else{
          callback();
        }
      };
      var checkPassword = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('密码不能为空'));
        }else{
          callback();
        }
      };
      return {
        ruleForm: {
          password: '',
          account:'',
        },
        rules: {
          password: [
            { validator: checkPassword, trigger: 'blur' }
          ],
          account: [
            { validator: checkAccount, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      async go(ruleForm){
        const {account,password} = this.ruleForm
        if(account&&password.trim()){
          var result =await login(account+"",password)
        }else{
          this.$message({
            message: '账号密码不能为空',
            type: 'warning',
            center: true,
            duration:1500,
            customClass:'uer-defined-style'//该样式
          });
        }
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
  }
</script>
<style>
  .loginall{
    background-image: url("data:image/jpeg;base64,");
    background-size: 100% 100%;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  @media screen and (max-width:499px){
    .login{
      background-color: rgba(255,255,255,0);
    }
    .login1{
      width: 300px;
      padding: 190px 0 10px;
    }
    .login1 p{
      font-size: 25px;
      height: 40px;
      line-height: 40px;
      margin-left: 45%;
      margin-bottom: 30px;
      font-weight: bold;
    }
  }
  @media screen and (min-width:500px){
    .login{
      width: 400px;
      margin: 250px auto;
      border: 1px solid rgb(242,242,242);
      background-color: rgba(255,255,255,0);
      border-radius: 7px;
    }
    .login1{
      width: 350px;
      padding: 30px 0 10px;
    }
    .login1 p{
      font-size: 25px;
      height: 40px;
      line-height: 40px;
      margin-left: 45%;
      margin-bottom: 20px;
      font-weight: bold;
    }
  }
</style>



