<template>
    <div class="mo">
      <div class="upload">
        <el-row style="padding-bottom: 10px;margin-left: 10px;margin-top: 10px">
          <el-upload
            class="avatar-uploader"
            ref="upload"
            :show-file-list="false"
            action=""
            :before-upload="beforeUpload"
            :on-change="handleChange"
            :auto-upload="false"
          >
            <el-button size="small" type="primary" icon="el-icon-upload2" style="height: 25px;font-size: 12px;">上传</el-button>
          </el-upload>
        </el-row>
        <div v-if="fileList.length==0">
          <p style="margin:0;margin-top: 30%;margin-left: 26%;display: inline-block;font-size: 18px;">传输列表为空</p>
        </div>
        <div v-for="(list,index) in fileList" :key="index" v-else>
          <li style="min-height: 50px;max-height: 70px;border-bottom: 1px solid rgb(230,230,230)">
            <i class="el-icon-folder" style="width:10%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px;"/>
            <span style="display: inline-block;width:10%;float: right;margin-top: 15px;margin-left: 2%">
              <i class="el-icon-upload-success el-icon-circle-check" style="color: greenyellow" v-if="list.succeed&&!list.error&&!list.stop"/>
              <i class="el-icon-refresh-right" style="color: deepskyblue" v-if="!list.succeed&&list.error||list.stop" @click="()=>go(index,list.file,true)"/>
              <i class="el-icon-close" style="color: black" @click="()=>cancel(index)" v-if="!list.succeed&&!list.error&&!list.stop"/>
            </span>
            <span style="display: inline-block;width: 15%;overflow: hidden;min-height: 16px;max-height: 32px;font-size: 12px;line-height:16px;overflow: hidden;float: right;margin-top: 15px;" :class="{'error':list.error||list.stop,'succeed':list.succeed,'native':!list.error&&!list.stop&&!list.succeed}">{{list.persent}}%</span>
            <div style="display: inline-block;width: 58%;margin-top: 10px;margin-bottom: 5px;margin-right: 3%;">
              <p style="display: inline-block;width: 100%;overflow: hidden;min-height: 16px;max-height: 32px;font-size: 14px;line-height:16px;overflow: hidden">{{list.fileRealName}}</p>
              <span style="display: inline-block;width: 45%;overflow: hidden;min-height: 15px;max-height: 15px;font-size: 12px;color: lightgrey">{{getNowFormatDate()}}</span>
              <span style="display: inline-block;width: 40%;overflow: hidden;min-height: 15px;max-height: 15px;font-size: 12px;color: lightgrey">{{countSize(list.fileSize)}}</span>
            </div>
          </li>
        </div>
      </div>
    </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data(){
      return {
        fileList: [],
        sources:[],
      }
    },
    methods:{
      getNowFormatDate() {
        var date = new Date();
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
         if (month >= 1 && month <= 9) {
               month = "0" + month;
           }
         if (strDate >= 0 && strDate <= 9) {
             strDate = "0" + strDate;
           }
         var currentdate = year + seperator1 + month + seperator1 + strDate;
         return currentdate;
      }, /*获取时间日期*/
      beforeUpload(){
        return true
      },
      handleChange(file,filelists){
        this.fileList.push(
          {
            fileRealName: file.name,
            fileSize: file.size,
            persent: 0,
            succeed: false,
            stop: false,
            error: false,
            file:file
          }
        )
        var index = this.fileList
        this.go(index.length-1,file)
      },
      cancel(index){
        if(this.sources[index]){//我先判断soucre是否存在，因为如果我打开弹框不作任何操作，点击取消按钮没有这一层判断的话，that.source.cancel('取消上传');会报错。
          this.sources[index].cancel('取消上传');//"取消上传"这几个字，会在上面catch（）的error中输出的，可以console看一下。
          console.log(this.sources[index])
          this.fileList[index].stop = true
        }
      },
      go (index,file,reload=false) {
        let self = this
        let cancelToken = axios.CancelToken;
        if(!reload)
          this.sources.push(cancelToken.source())
        else{
          this.sources.splice(index,1,cancelToken.source())
          this.fileList.splice(index,1,
            {
              fileRealName: file.name,
              fileSize: file.size,
              persent: 0,
              succeed: false,
              stop: false,
              error: false,
              file:file
            }
          )
        }
        let param = new FormData(); //创建form对象，私有，无法直接获取，只能通过get来查看其中的元素
        param.append('uploadFile',file.raw)
        let config = {
          headers:{
            'Content-Type':'multipart/form-data',
          },
          cancelToken:this.sources[index].token,//取消事件
          transformRequest: [function (data) {
            return data
          }],
          onUploadProgress: progressEvent => {
            let persent = (progressEvent.loaded / progressEvent.total * 100 | 0)
            self.fileList[index].persent = persent
          },
        }
        axios.post('http://47.112.132.177:8092/file/upload',param,config)
          .then(response=>{
            var result = response.data
            console.log(result)
            if(result.status == 200)
            {
              self.fileList[index].succeed = true
              this.reReq = result.obj.fileType
            }else{
              self.fileList[index].stop = true
              self.fileList[index].error = true
              this.$message({
                message: '上传失败',
                type: 'error',
                duration:'1000'
              });
            }
          }).catch(err => {
          console.log(err)
        })
      },
      countSize(size){
        if(size/1024/1024/1024>0.5){
          return ((size/1024/1024/1024)+'').split('.')[0]+'.'+((size/1024/1024/1024)+'').split('.')[1].slice(0,2)+'G'
        } else if(size/1024/1024>0.5){
          return ((size/1024/1024)+'').split('.')[0]+'.'+((size/1024/1024)+'').split('.')[1].slice(0,2)+'M'
        }else if(size/1024>0.5){
          return ((size/1024)+'').split('.')[0]+'.'+((size/1024)+'').split('.')[1].slice(0,2)+'KB'
        }else{
          return size + 'B'
        }
      },
    },
    mounted () {
      this.$emit('choicetypemo','20000')
    }
  }
</script>

<style>
  @media screen and(max-width: 499px){
    .mo{
      display: block;
    }
    .error{
      color: red;
    }
    .native{
      color: deepskyblue;
    }
    .succeed{
      color: lightgreen;
    }
  }
  @media screen and(min-width: 500px) {
    .mo {
      display: none;
    }
  }
</style>



