<template>
  <div>
    <div class="pc">
      <div class="rubbish">
        <div><p style="margin-left: 15px;margin-top: 20px;margin-bottom: 10px">回收站:</p></div>
        <div class="list_type_name1" style="border-bottom: 1px solid rgb(243,243,243);border-top: 1px solid rgb(243,243,243)">
          <div class="list_type_name" style="width: 60%"><span style="padding-left: 30px;display: inline-block;height: 40px;line-height: 40px">文件名</span></div>
          <div class="list_type_name" style="width: 10%"><span style="padding-left: 15px;display: inline-block;height: 40px;line-height: 40px">大小</span></div>
          <div class="list_type_name" style="width: 15%"><span style="padding-left: 15px;display: inline-block;height: 40px;line-height: 40px">上传日期</span></div>
          <div class="list_type_name" style="width: 15%"><span style="padding-left: 15px;display: inline-block;height: 40px;line-height: 40px">作者</span></div>
        </div>
        <div v-loading="rubbishLoding" class="main_list">
          <div v-if="!rubbishLoding">
            <div v-if="rubbishs.length>0">
              <div v-for="(rubbish,index) in rubbishs" :key="index">
                <li :id="'rubbish'+rubbish.id" class="rubbish_lists" @mouseover="()=>showP(rubbish.id)" @mouseout="()=>hideP(rubbish.id)">
                  <i v-if="rubbish.fileType==1" class="el-icon-picture-outline" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <i v-if="rubbish.fileType==2" class="el-icon-video-camera" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <i v-if="rubbish.fileType==4" class="el-icon-headset" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <i v-if="rubbish.fileType==3" class="el-icon-document" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <i v-if="rubbish.fileType==5" class="el-icon-folder" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <i v-if="rubbish.fileType==6" class="el-icon-folder" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <i v-if="rubbish.fileType==7" class="el-icon-folder" style="float: left;font-size: 26px!important;color: rgb(77,151,255);"/>
                  <span style="display: inline-block;width: 15%;float:right;font-size: 14px;margin-top: 13px;text-indent: 1em;overflow: hidden;height: 20px;">{{rubbish.createBy}}</span>
                  <span style="display: inline-block;width: 15%;float:right;font-size: 14px;margin-top: 13px;text-indent: 1em;overflow: hidden;height: 20px;">{{rubbish.fileUrlTime.split("T")[0]}}</span>
                  <span style="display: inline-block;width: 10%;float:right;font-size: 14px;margin-top: 13px;text-indent: 1em;overflow: hidden;height: 20px;">{{countSize(rubbish.fileSize)}}</span>
                  <span class="list_name" style="display: inline-block;width: 45%;margin-top: 13px;overflow: hidden;height: 20px;" @click="()=>clickVideo(rubbish.id)">{{rubbish.fileRealName}}</span>
                  <i :id="'rp'+rubbish.id" class="el-icon-refresh-left list_hover down_list" style="font-size: 20px!important;cursor: pointer;position: absolute;left: 52%;color: rgb(77,151,255);" v-show="false" @click="()=>reUpload(rubbish.id)"/>
                </li>
              </div>
            </div>
            <div v-else>
              <p style="margin:0;margin-top: 20%;margin-left: 38%;display: inline-block;font-size: 28px;">回收站为空</p>
            </div>
          </div>
        </div>
        <div class="block" v-if="rubbishPage">
          <el-pagination
            layout="prev, pager, next"
            :total="rubbishPage.totalSize"
            :current-page="rubbishPage.pageNum"
            :hide-on-single-page="true"
            :page-size="pageSize"
            @current-change="(currentPage)=>reqMessages(currentPage)"
            style="margin-left: 40%"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="mo">
      <div class="mo_image">
        <div class="mo_allName">
          <p>回收站:</p>
        </div>
        <div class="main" v-loading="rubbishLoding">
          <div v-if="!rubbishLoding">
            <div v-if="mo.rubbishs.length>0">
              <div v-for="(rubbish,index) in mo.rubbishs" :key="index">
                <li :id="'rubbish'+rubbish.id" style="min-height: 50px;max-height: 70px;border-bottom: 1px solid rgb(230,230,230)">
                  <i v-if="rubbish.fileType==1" class="el-icon-picture-outline" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <i v-if="rubbish.fileType==2" class="el-icon-video-camera" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <i v-if="rubbish.fileType==4" class="el-icon-headset" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <i v-if="rubbish.fileType==3" class="el-icon-document" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <i v-if="rubbish.fileType==5" class="el-icon-folder" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <i v-if="rubbish.fileType==6" class="el-icon-folder" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <i v-if="rubbish.fileType==7" class="el-icon-folder" style="width:12%;float: left;font-size: 22px!important;color: rgb(77,151,255);text-align: center;margin-top: 12px"/>
                  <div style="display: inline-block;width: 63%;margin-top: 10px;margin-bottom: 5px;margin-right: 5%;">
                    <p style="display: inline-block;width: 100%;overflow: hidden;min-height: 16px;max-height: 32px;font-size: 13px;line-height:16px;overflow: hidden">{{rubbish.fileRealName}}</p>
                    <span style="display: inline-block;width: 45%;overflow: hidden;min-height: 15px;max-height: 15px;font-size: 12px;color: lightgrey">{{rubbish.fileUrlTime.split("T")[0]}}</span>
                    <span style="display: inline-block;width: 40%;overflow: hidden;min-height: 15px;max-height: 15px;font-size: 12px;color: lightgrey">{{countSize(rubbish.fileSize)}}</span>
                  </div>
                  <span :id="'rp'+rubbish.id" style="display: inline-block;width:18%;position: relative">
                    <i class="el-icon-refresh-left list_hover" style="font-size: 20px!important;margin: 0px 7px;color: rgb(77,151,255);position: absolute;top: -30px" @click="()=>reUpload(rubbish.id)"/>
                  </span>
                </li>
              </div>
            </div>
            <div v-else>
              <p style="margin:0;margin-top: 30%;margin-left: 24%;display: inline-block;font-size: 18px;">回收站为空</p>
            </div>
          </div>
          <div class="block" v-if="mo.rubbishPage">
            <el-pagination
              layout="prev, pager, next"
              :total="mo.rubbishPage.totalSize"
              :current-page="mo.rubbishPage.pageNum"
              :page-size="mo.pageSize"
              :hide-on-single-page="true"
              @current-change="(currentPage)=>reqMessages(currentPage)"
              :small="true"
              style="float:right;margin-right: 3%;margin-top: 10px"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import '../assets/my-message.css'
  import {RewTypeEnable,ChangeEnable} from '../api/types'
  export default {
    data(){
      return {
        rubbishLoding:false,
        rubbishs:[],
        rubbishId:0,
        page:1,
        pageSize:17,
        enable:0,
        rubbishPage:[],
        status:false,
        mo:{
          rubbishPage:null,
          page:1,
          pageSize:11,
          rubbishs:[],
        }
      }
    },
    async mounted () {
      this.$emit('choiceType','2')
      this.$emit('choicetypemo','10000')
      this.reqMessages(1)
    },
    methods:{
      showP(e){
        $('#rp'+e).show()
      },
      hideP(e){
        $('#rp'+e).hide()
      },
      async reqMessages(page){
        this.rubbishLoding = true
        var mo = this.mo
        var result = await RewTypeEnable(this.enable,page,this.pageSize)
        if(result.status){
          if(result.status == 200){
            result = result.obj
            this.rubbishs = result.content
            this.rubbishPage = result
          }
        }else{
          this.$message(
            {
              type: 'error',
              message: '暂时无法获取数据，请刷新重试',
              duration: 2000,
            }
          )
        }
        var result1 = await RewTypeEnable(this.enable,page,mo.pageSize)
        if(result1.status){
          if(result1.status == 200){
            result1 = result1.obj
            mo.rubbishs = result1.content
            mo.rubbishPage = result1
          }
        }else{
          this.$message(
            {
              type: 'error',
              message: '暂时无法获取数据，请刷新重试',
              duration: 2000,
            }
          )
        }
        this.rubbishLoding = false
      },
      reUpload(id){
        this.rubbishId = id
        this.$confirm('确定还原选中的文件吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          var result = await ChangeEnable(1,this.rubbishId)
          if(result.status==200){
            this.reqMessages(1)
          }else{
            this.$message(
              {
                type: 'error',
                message: '还原失败，请重试',
                duration: 1000,
              }
            )
          }
        }).catch(() => {
          return false
        });
      },
      clickVideo(id){
        this.rubbishId = id
      },
      countSize(size){
        if(size/1024/1024/1024>0.5){
          return ((size/1024/1024/1024)+'').split('.')[0]+'.'+((size/1024/1024/1024)+'').split('.')[1].slice(0,2)+'G'
        } else if(size/1024/1024>0.5){
          return ((size/1024/1024)+'').split('.')[0]+'.'+((size/1024/1024)+'').split('.')[1].slice(0,2)+'M'
        }else if(size/1024>0.5){
          return ((size/1024)+'').split('.')[0]+'.'+((size/1024)+'').split('.')[1].slice(0,2)+'KB'
        }else{
          return size + 'B'
        }
      },
    },
  }
</script>

<style scoped>
  @media screen and (max-width: 499px){
    .block{
      height: 45px;
      padding-top: 5px;
    }
    .block:after,.block:before{
      display: table;
      content: '';
      clear: both;
    }
    .pc{
      display: none;
    }
    .mo{
      display: block;
    }
    .mo_image{
      height: 100%;
      width: 100%;
      background-color: white;
    }
    .mo_allName{
      height: 30px;
      margin-bottom: 10px;
    }
    .mo_allName p{
      height: 30px;
      line-height: 30px;
      font-size: 15px;
      font-weight: bold;
      margin-left: 10px;
    }
    .main{
      min-height: 300px;
    }
  }
  @media screen and (min-width: 500px) {
    .pc {
      display: block;
    }

    .mo {
      display: none;
    }

    .rubbish_lists{
      height: 40px;
      position: relative;
      border-bottom: 1px solid rgb(243,243,243);
    }
    .rubbish_lists:hover,.list_type_name:hover{
      background-color: rgb(244,251,255);
    }
    .main_list{
      min-height: 700px
    }
    .list_type_name{
      float: left;
      height: 40px;
      color: gray;
      cursor: pointer;
    }
    .list_type_name1:after,.list_type_name1:before{
      display: table;
      content: '';
      clear: both;
    }
    .rubbish_lists .list_name{
      font-size: 14px;
      margin-left: 10px;
      position: relative;
      cursor: pointer;
    }
    .rubbish_lists .list_name:hover{
      color: rgb(64,158,254);
      text-decoration: underline;
    }
    .rubbish_lists i{
      line-height: 40px;
      height: 40px;
      margin-left: 30px;
    }
  }
  .rubbish_lists span,.list_type_name{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
</style>



